import React from "react";
import Card from "react-bootstrap/Card";
import { CardData } from "../types";

type ConferenceCardProps = {
  card: CardData;
};

const ConferenceCard: React.FC<ConferenceCardProps> = ({ card }) => {
  return (
    <Card className={"card"} text={"dark"}>
      <Card.Header className={"cardHeader"}>{card.time}</Card.Header>

      <Card.Body className={"cardBody"} style={{ alignItems: "flex-start" }}>
        <Card.Title className={"cardTitle"}>{card.title}</Card.Title>
        <Card.Subtitle className={"cardSubtitle"}>
          <Card.Link className={"a cardLink"} href={card.contact}>
            {card.speaker}
          </Card.Link>
        </Card.Subtitle>
        <Card.Text>{card.details}</Card.Text>
      </Card.Body>
    </Card>
  );
};

export default ConferenceCard;
