import React from "react";
import Balancer from "react-wrap-balancer";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { getDate } from "../../../hooks/useEditionData";
import { currentEdition, Edition } from "../../../data";
import { conferenceCards } from "../res/conferenceCards";
import ConferenceCard from "./conferenceCard";

import * as styles from "./conference.module.css";

type ConferenceProps = {
  edition: Edition;
};

const Conference: React.FC<ConferenceProps> = ({ edition }) => {
  const { t } = useTranslation();
  const date = getDate(edition, "conference");

  return (
    <div className={`page ${styles.conference}`} id={"conference"}>
      <h1 className={"title"}>
        <Balancer>{t("title")}</Balancer>
      </h1>
      <h2 className={"date"}>
        <Balancer>{date}</Balancer>
      </h2>
      {edition < currentEdition && (
        <ul className={"ul"}>
          {conferenceCards[edition].map((item, index) => (
            <ConferenceCard key={"conferenceCard" + index} card={item} />
          ))}
        </ul>
      )}

      {edition < currentEdition && (
        <p className={`p`} style={{ textAlign: "center" }}>
          {t("endDescription")}
        </p>
      )}
    </div>
  );
};

export default Conference;
